.mobile {
  margin-right: auto;
}

.container {
  z-index: 1;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  position: relative;
}

.cards {
  z-index: 2;
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
  position: relative;
}

.card {
  opacity: 0;
  margin: 10px;
  padding: 10px;
  transition: transform 1s, opacity 1s;
  animation: .5s forwards cardEnter;
  position: relative;
  transform: translateY(100%);
}

@keyframes cardEnter {
  from {
    opacity: 0;
    transform: translateY(100%)translateZ(-100px);
  }

  to {
    opacity: 1;
    transform: translateY(0%)translateZ(0);
  }
}

.cards-container {
  perspective: 1000px;
  flex-direction: column-reverse;
  align-items: flex-start;
  display: flex;
}

.card:nth-child(2n) {
  transform: translateZ(-200px);
}

.card:hover {
  transform: scale(1.04) !important;
}

.card:hover:before {
  content: "";
  z-index: -1;
  filter: blur(2px);
  border: 5px solid purple;
  border-top-left-radius: 50px;
  border-bottom-right-radius: 50px;
  position: absolute;
  inset: -1%;
  box-shadow: 0 2px 10px purple !important;
}

.card img {
  border-top-left-radius: 50px;
  border-bottom-right-radius: 50px;
  width: 100%;
  height: auto;
}

.pagination {
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

.pagination a {
  background-color: #f5f5f5;
  margin: 0 5px;
  padding: 5px 10px;
  text-decoration: none;
}

.modal {
  z-index: 1;
  background-color: #0006;
  width: 100%;
  height: 100%;
  padding-top: 100px;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content {
  background-color: #f5f5f5;
  border-top-left-radius: 300px;
  border-bottom-right-radius: 300px;
  flex-direction: column;
  align-items: center;
  width: 930px;
  height: 1023px;
  margin: auto;
  padding: 20px;
  display: flex;
}

.close {
  color: #4c00fe;
  float: right;
  cursor: pointer;
  width: 25px;
  height: 25px;
  margin-left: auto;
  font-size: 28px;
  font-weight: bold;
}

.modal-details {
  align-items: center;
  display: flex;
}

.modal-poster {
  width: 360px;
  height: 641px;
  margin-top: 250px;
}

.modal-container {
  flex-direction: column;
}

.modal-info {
  margin-left: 20px;
}

.modal-buy-tickets {
  margin-top: 20px;
  margin-left: 20px;
}

.modal-more-info {
  text-align: center;
  border: 1px solid #4c00fe;
  border-radius: 5px;
  width: 255px;
  height: 40px;
  margin-top: 20px;
}

.more_info {
  color: #4c00fe;
  margin-top: 10px;
  font-family: Montserrat;
  font-size: 16px;
  line-height: 19.5px;
  text-decoration: none;
}

@media (min-width: 1280px) {
  .cards {
    width: 1200px;
  }

  .card {
    width: 270px;
    height: 320px;
  }

  .card img {
    width: 255px;
    height: 220px;
  }
}

@media (min-width: 768px) and (max-width: 1279px) {
  .card {
    width: 152px;
    height: 211px;
  }

  .card img {
    width: 141px;
    height: 120px;
  }

  .modal-content {
    width: 595px;
    height: 831px;
  }

  .modal-poster {
    width: 233px;
    height: 416px;
    margin-top: 160px;
  }
}

@media (max-width: 767px) {
  .mobile {
    flex-direction: column;
  }

  .card {
    width: 130px;
    height: 208px;
  }

  .card img {
    width: 120px;
    height: 120px;
  }

  .modal-content {
    width: 272px;
    height: 915px;
  }

  .modal-poster {
    border-radius: 50%;
    width: 102px;
    height: 102px;
    margin-top: 160px;
  }
}

